<template>
  <div ref="elmt"
      v-if="type === 'div'"
      class="astnode"
      tabindex="-1"
      @keyup.stop="onKey"
  ><slot></slot></div
  ><span ref="elmt"
      v-else
      class="astnode"
      tabindex="-1"
      @keyup.stop="onKey"
    ><slot></slot></span>
</template>

<script>
    export default {
        name: "Selectable",
        props: {
          type: String,
        },
        methods: {
          onKey: function (evt) {
          },
          focus: function () {
            this.$refs.elmt.focus();
          }
        },
    }
</script>

<style scoped>

</style>



