<template>
  <component v-bind:is="data.type" :data="data" :parent="parent" ref="elmt" :reqPrecedence="reqPrecedence"></component>
</template>

<script>
  import BinOp from "./BinOp";
  import Num from "./Num";
  export default {
    name: "nodecomp",
    components: {BinOp, Num},
    props: {
      data: Object,
      parent: Object,
      reqPrecedence: Number,
    },
    methods: {
      get_node: function () {
        return this.$refs.elmt;
      }
    },
  }
</script>

<style scoped>

</style>



