<template>
  <input
      v-if="editing"
      ref="input"
      class="astnode editing"
      :class="{invalidInput: !this.validIdentifier,}"
      :style="{width: content_size+'ch'}"
      :placeholder="placeholder"
      v-model="data.s"
      @blur="editing=false"
      @keyup.enter.stop="sel_end"
      @keydown.stop="onKey"
    ><span
      class="astnode"
      tabindex="-1"
      ref="input2"
      v-else
      style="user-select: none; -moz-user-select: none;"
      :class="{
        placeholder: this.data.s.length === 0,
        invalidInput: !this.validIdentifier,
      }"
      v-text="content_or_placeholder"
      @dblclick="sel"
      @keyup.enter.stop="sel"
    ></span>
</template>

<script>
  import astNode from "./astNode"
  export default {
    name: "Identifier",
    mixins: [astNode],
    props: ["check", "data"],
    data: function () {
      return {
        editing: false,
        placeholder: "<identifier>",
      }
    },
    methods: {
      sel: function() {
        this.editing = true;
        this.$nextTick(function () {
          this.$refs.input.select();
        });
      },
      sel_end: function() {
        this.editing = false;
        this.$nextTick(function () {
          this.$refs.input2.focus();
        });
      },
      onKey: function (evt) {
        if (evt.key === "Tab"){
          evt.preventDefault();
          evt.stopPropagation();
        }
        if (evt.key.match(/\W$/)){
          evt.preventDefault();
        }
      }
    },
    computed: {
      content_size: function () {
        let clen = this.data.s.length;
        return clen > 0 ? clen : this.placeholder.length;
      },
      validIdentifier: function () {
        if (!(this.check === "identifier"))
          return true;
        let reserved_names = [
          "False", "class", "finally", "is", "return", "None", "continue", "for", "lambda", "try", "True", "def",
          "from", "nonlocal", "while", "and", "del", "global", "not", "with", "as", "elif", "if", "or", "yield",
          "assert", "else", "import", "pass", "break", "except", "in", "raise",
        ];
        return this.data.s.match(/^[a-z_][a-z0-9_]*$/i) && reserved_names.indexOf(this.data.s)<0;
      },
      content_or_placeholder: function () {
        return this.data.s.length? this.data.s: this.placeholder;
      }
    }
  }
</script>

<style scoped>



  .editing {
    outline: 1px solid black;
  }

  .placeholder {
    color: gray;
  }

</style>



